import React, { useContext } from "react";
import { UserContext } from "./UserContext";
import Autocomplete, { createFilterOptions } from '@material-ui/lab/Autocomplete';
//GENERAL
import { TextField, Grid } from "@material-ui/core";
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
//USER PREFERENCES
export default props => {
  const [state] = useContext(UserContext);
  const { user,errors } = state;
  var options=[];
  const [areas,setareas]=React.useState([]);
  const[chec,setchec]=React.useState(false);
  const handleChange=()=>{
    setchec(!chec)
    if(chec!==true){
      user.OAZIP = user.HAZIP
      user.OAaddressLine1 = user.HAaddressLine1
      user.OAArea = user.HAArea
      user.OADistrict = user.HADistrict
      user.OACity = user.HACity
      user.OAState = user.HAState
      user.OACountry = user.HACountry

      console.log(
        //user.fullName,
        //   user.companyName,
        //   user.designation,
        //   user.PAN,
        //   user.GST,
        //   user.email,
        //   user.Country,
        //   user.countryCode,
        //   user.mobileNumber,
        //   user.phoneCountryCode,
        //   user.phoneNumber,
        //   user.password,
        //   user.confirmPassword,
        //   user.HAZIP,
        //   user.HAaddressLine1,
        // user.HAArea,
        // user.HADistrict,
        // user.HACity,
        // user.HAState,
        // user.HACountry,

        //  user.OAZIP,
        //   user.OAaddressLine1,
        user.OAArea,
        area.childNodes[1].firstElementChild.value
        // user.OADistrict,
        // user.OACity,
        // user.OAState,
        // user.OACountry,
      )

      zi.childNodes[1].firstElementChild.value = user.HAZIP
      addres.childNodes[1].firstElementChild.value = user.HAaddressLine1
      areas.push(user.HAArea)
      area.childNodes[1].firstElementChild.value=user.HAArea
      district.childNodes[1].firstElementChild.value = user.HADistrict
      city.childNodes[1].firstElementChild.value = user.HACity
      circle.childNodes[1].firstElementChild.value = user.HAState
      country.childNodes[1].firstElementChild.value = user.HACountry
    }
    else{
      user.OAZIP = ""
      user.OAaddressLine1 = ""
      user.OAArea = ""
      user.OADistrict = ""
      user.OACity = ""
      user.OAState = ""
      user.OACountry = ""

      zi.childNodes[1].firstElementChild.value = ""
      addres.childNodes[1].firstElementChild.value = ""
      area.childNodes[1].firstElementChild.value = ""
      district.childNodes[1].firstElementChild.value = ""
      city.childNodes[1].firstElementChild.value = ""
      circle.childNodes[1].firstElementChild.value = ""
      country.childNodes[1].firstElementChild.value = ""
    }
  }
  let zi = React.useRef(null)
  let addres = React.useRef(null)
  let area = React.useRef(null)
  let district=React.useRef(null)
  let city = React.useRef(null)
  let circle = React.useRef(null)
  let country = React.useRef(null)
  const [dis, setdis] = React.useState("")
  const [cit, setcit] = React.useState("")
  const [cir, setcir] = React.useState("")
  const [coun, setcon] = React.useState("")
  let getAdress = (event) => {
    const val = "https://api.postalpincode.in/pincode/" + (event.target.value);
    console.log(val);
    fetch(val)
      .then(res => res.json())
      .then(res => {
        options=res;
        console.log(options);
        if(options[0].Status=="Success"){

              setdis(options[0].PostOffice[0].District);
              setcit(options[0].PostOffice[0].Region);
              setcir(options[0].PostOffice[0].Circle);
              setcon(options[0].PostOffice[0].Country);
          let a=[];
          options[0].PostOffice.map(opt=>{
            a.push(opt.Name);
          })
          setareas(a);
        }
      },
        (e) => { console.warn('fetch failure',e) }
      )
  }
  return (
    
    <Grid container spacing={2}>
      <Grid item xs={12}>
      <FormControlLabel
        control={
          <Checkbox
            checked={chec}
            onChange={handleChange}
            name="Same"
            color="primary"
          />
        }
        label="The same as resedential address"
      />
      </Grid>
        <Grid item xs={2}>
          <TextField ref={(el) => { zi = el; }}
            placeholder='ZIP code'
            name='OAZIP'
            label='ZIP Code'
          value={user.OAZIP}
            variant='outlined'
            onChange={getAdress}
            InputLabelProps={{
              shrink: true
            }}
            required
            inputProps={{
              maxLength:6,
              pattern:"^[1-9][0-9]{5}$"
            }}
          error={!!errors["PostalAddress.ZIP"]}
            fullWidth
          />
        </Grid> 
      <Grid item xs={10}>
        <TextField ref={(el) => {addres = el; }}
          placeholder='Type your Address here'
          name='OAaddressLine1'
          label='Address Line'
          onFocusCapture={()=>{
            district.childNodes[1].firstElementChild.value = dis
            user.OADistrict=dis
            city.childNodes[1].firstElementChild.value = cit
            user.OACity = cit
            circle.childNodes[1].firstElementChild.value = cir
            user.OAState = cir
            country.childNodes[1].firstElementChild.value = coun
            user.OACountry = coun
          }}
          value={user.OAaddressLine1}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 50
          }}
          error={!!errors["username"]}
          fullWidth
        />
    </Grid>
      <Grid item xs={6}>
        <Autocomplete
          freeSolo
          id="free-solo-2-demo"
          disableClearable
          value={user.OAArea}
          options={areas.map((option) => option)}
          renderInput={(params) => (
            <TextField ref={(el) => { area = el; }}
              {...params}
              placeholder='Enter your area here'
              name='OAArea'
              label='Area'
              value={user.OAArea}
              variant='outlined'
              InputLabelProps={{
                shrink: true
              }}
              required
              error={!!errors["Country"]}
              fullWidth
              InputProps={{ ...params.InputProps, type: 'search' }}
            />
          )}
        />
      </Grid>
      <Grid item xs={6}>
        <TextField
          ref={(el) => { district = el; }}
          placeholder='Enter Your District'
          name='OADistrict'
          label='District'
          value={user.OADistrict}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 18
          }}
          error={!!errors["username"]}
          fullWidth
        />
      </Grid>
      <Grid item xs={6}>
        <TextField
          ref={(el) => { city = el; }}
          placeholder='Enter Your City'
          name='OACity'
          label='City'
          value={user.OACity}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 18
          }}
          error={!!errors["username"]}
          fullWidth
        />
      </Grid>
      <Grid item xs={6}>
        <TextField
          ref={(el) => { circle = el; }}
          placeholder='Type your State'
          name='OAState'
          label='State'
          value={user.OAState}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 25
          }}
          error={!!errors["username"]}
          fullWidth
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          ref={(el) => { country = el; }}
          placeholder='Type your Country'
          name='OACountry'
          label='Country'
          value={user.OACountry}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 25
          }}
          error={!!errors["username"]}
          fullWidth
        />
      </Grid>
  </Grid>
  );
};
