import React, { useContext } from "react";
import { UserContext } from "./UserContext";
import Autocomplete, { createFilterOptions } from '@material-ui/lab/Autocomplete';
//GENERAL
import { TextField, Grid } from "@material-ui/core";

//USER PREFERENCES
export default props => {
  const [state] = useContext(UserContext);
  const { user,errors } = state;
  var options=[];
  const [areas,setareas]=React.useState([]);

  let district=React.useRef(null)
  let city = React.useRef(null)
  let circle = React.useRef(null)
  let country = React.useRef(null)
  const [dis, setdis] = React.useState("")
  const [cit, setcit] = React.useState("")
  const [cir, setcir] = React.useState("")
  const [coun, setcon] = React.useState("")
  let getAdress = (event) => {
    const val = "https://api.postalpincode.in/pincode/" + (event.target.value);
    console.log(val);
    fetch(val)
      .then(res => res.json())
      .then(res => {
        options=res;
        console.log(options);
        if(options[0].Status=="Success"){

              setdis(options[0].PostOffice[0].District);
              setcit(options[0].PostOffice[0].Region);
              setcir(options[0].PostOffice[0].Circle);
              setcon(options[0].PostOffice[0].Country);
          let a=[];
          options[0].PostOffice.map(opt=>{
            a.push(opt.Name);
          })
          setareas(a);
        }
      },
        (e) => { console.warn('fetch failure',e) }
      )
  }
  return (

    <Grid container spacing={2}>
        <Grid item xs={2}>
          <TextField
            placeholder='ZIP code'
            name='HAZIP'
            label='ZIP Code'
          value={user.HAZIP}
            variant='outlined'
            onChange={getAdress}
            InputLabelProps={{
              shrink: true
            }}
            required
            inputProps={{
              maxLength:6,
              pattern:"^[1-9][0-9]{5}$"
            }}
          error={!!errors["PostalAddress.ZIP"]}
            fullWidth
          />
        </Grid> 
      <Grid item xs={10}>
        <TextField
          placeholder='Type your Address here'
          name='HAaddressLine1'
          label='Address Line'
          onFocusCapture={()=>{
            district.childNodes[1].firstElementChild.value = dis
            user.HADistrict=dis
            city.childNodes[1].firstElementChild.value = cit
            user.HACity = cit
            circle.childNodes[1].firstElementChild.value = cir
            user.HAState = cir
            country.childNodes[1].firstElementChild.value = coun
            user.HACountry = coun
          }}
          value={user.HAaddressLine1}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 50
          }}
          error={!!errors["username"]}
          fullWidth
        />
    </Grid>
      <Grid item xs={6}>
        <Autocomplete
          freeSolo
          id="free-solo-2-demo"
          disableClearable
          value={user.HAArea}
          options={areas.map((option) => option)}
          onChange={(event,value) => { user.HAArea = value; console.log(user.HAArea); }}
          renderInput={(params) => (
            <TextField
              {...params}
              placeholder='Enter your area here'
              name='HAArea'
              label='Area'
              value={user.HAArea}
              variant='outlined'
              InputLabelProps={{
                shrink: true
              }}
              required
              onChange={(event) => { user.HAArea = event.target.value; console.log(user.HAArea); }}
              error={!!errors["Country"]}
              fullWidth
              InputProps={{ ...params.InputProps, type: 'search' }}
            />
          )}
        />
      </Grid>
      <Grid item xs={6}>
        <TextField
          ref={(el) => { district = el; }}
          placeholder='Enter Your District'
          name='HADistrict'
          label='District'
          value={user.HADistrict}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 18
          }}
          error={!!errors["username"]}
          fullWidth
        />
      </Grid>
      <Grid item xs={6}>
        <TextField
          ref={(el) => { city = el; }}
          placeholder='Enter Your City'
          name='HACity'
          label='City'
          value={user.HACity}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 18
          }}
          error={!!errors["username"]}
          fullWidth
        />
      </Grid>
      <Grid item xs={6}>
        <TextField
          ref={(el) => { circle = el; }}
          placeholder='Type your State'
          name='HAState'
          label='State'
          value={user.HAState}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 25
          }}
          error={!!errors["username"]}
          fullWidth
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          ref={(el) => { country = el; }}
          placeholder='Type your Country'
          name='HACountry'
          label='Country'
          value={user.HACountry}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 25
          }}
          error={!!errors["username"]}
          fullWidth
        />
      </Grid>
  </Grid>
  );
};
