import React, { createContext, useState } from "react";
export const UserContext = createContext([{}, () => {}]);

export default props => {
  const [state, setState] = useState({
    user: {
      fullName: "",
      companyName:"",
      designation:"",
      dateOfBirth: new Date().toISOString().slice(0, 10),
      PAN: "",
      GST: "",
      email: "",
      Country: "India",
      countryCode: "+91",
      mobileNumber: "",
      instituteDepartment: "",
      phoneCountryCode: "+91",
      phoneNumber: "",
      password: "",
      confirmPassword: "",
        HAZIP:"",
        HAaddressLine1: "",
        HAArea:"",
        HADistrict:"",
        HACity:"",
        HAState:"",
        HACountry:"",

        OAZIP: "",
        OAaddressLine1: "",
        OAArea:"",
        OADistrict:"",
        OACity:"",
        OAState:"",
        OACountry:"",
      image: "",//convert to base 64
      acceptTerms: false,
    },
    errors: {}
  });
  return (
    <UserContext.Provider value={[state, setState]}>
      {props.children}
    </UserContext.Provider>
  );
};
