import React, { useContext,useState } from "react";
//GENERAL
import { TextField, Grid } from "@material-ui/core";
import { isWidthDown } from "@material-ui/core/withWidth";
import Autocomplete, { createFilterOptions } from '@material-ui/lab/Autocomplete';
//CONTEXT
import { UserContext } from "./UserContext";
import InputBase from '@material-ui/core/InputBase';

export default props => {
  const[img,setImg]=React.useState(null)
  let country=React.useRef(null)
  let countryC = React.useRef(null)
  let countryCp = React.useRef(null)
  const [state] = useContext(UserContext);
  const { user, errors } = state;
  const dateLimit = new Date();
  dateLimit.setFullYear(dateLimit.getFullYear() - 18);
  const filterOptions = createFilterOptions({
    limit: 5,
  });
  const handleChanges=(e)=>{
    setTimeout(() => {
      let CountryVal = country.childNodes[1].firstElementChild.value
        CountryList.map((option) => { if (option.name === CountryVal) { 
        user.countryCode = option.Iso; 
        user.phoneCountryCode = option.Iso; 
        countryC.childNodes[1].firstElementChild.value=option.Iso
        countryCp.childNodes[1].firstElementChild.value = option.Iso
      }
      })}, 1000);  
  };

  const handleCardNumber = (event) => {
    let formattedText = this.event.value.split(' ').join('');
    if (formattedText.length > 0) {
      formattedText = formattedText.match(new RegExp('.{1,4}', 'g')).join(' ');
    }
    console.log(formattedText);
    this.event.value=formattedText;
  }
  return (
    <Grid container spacing={2}>
      <Grid item xs={8} container>
      <Grid item xs={12}>
        <TextField
          placeholder='Type your Full Name here'
          name='fullName'
          label='Name'
          value={user.fullName}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          required
          inputProps={{
            minLength: 3,
            maxLength: 35
          }}
          error={!!errors["username"]}
          fullWidth
        />
        
      </Grid> 
      <Grid item xs={12}>
        <TextField
          placeholder='Type your email here'
          name='email'
          label='Email'
          value={user.email}
          type='email'
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          error={!!errors["email"]}
          required
          fullWidth
        />
      </Grid>
      </Grid>
      <Grid item xs={4}>
        <div style={{ textAlign: "center" }}>Add Image</div>
        <InputBase
          name="Image"
          type="file"
          value={user.image}
          inputProps={{ 'aria-label': 'naked' }}
          error={!!errors["image"]}
          onChange={(event) => { setImg(URL.createObjectURL(event.target.files[0]))}}
        />
        <img src={img} height={img === null ? "0" : "65%"} width={img === null ? "0" : "65%"} style={{marginLeft:"10%"}} />
      </Grid> 
      <Grid item xs={6}>
        <TextField
          placeholder="Type your Company's Name here"
          name='companyName'
          label='Company Name'
          value={user.companyName}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          inputProps={{
            minLength: 3,
            maxLength: 18
          }}
          error={!!errors["companyName"]}
          fullWidth
        />
      </Grid> 
      <Grid item xs={6}>
        <TextField
          placeholder="Type your Designation here"
          name='designation'
          label='Designation'
          value={user.designation}
          variant='outlined'
          InputLabelProps={{
            shrink: true
          }}
          inputProps={{
            minLength: 3,
            maxLength: 10
          }}
          error={!!errors["designation"]}
          fullWidth
        />
      </Grid>
     
      {/* <Grid item xs={4}>
        <TextField
          type='date'
          name='birthdate'
          id='birthdate'
          label='Birthdate'
          defaultValue={user.birthdate}
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          inputProps={{
            min: "1920-01-01",
            max: dateLimit.toISOString().slice(0, 10)
          }}
          error={!!errors["birthdate"]}
          required
          fullWidth={isWidthDown("sm")}
        />
      </Grid> */}
      
      <Grid item xs={6}>
        <TextField
          placeholder='Type in your Department'
          name='instituteDepartment'
          label='Institute Department'
          value={user.instituteDepartment}
          type='text'
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          error={!!errors["instituteDepartment"]}
          fullWidth
        />
      </Grid>

      <Grid item xs={6}>
        <Autocomplete
          id="free-solo-2-demo"
          disableClearable
          options={CountryList.map((option) => option.name)}
          filterOptions={filterOptions}
          onChange={handleChanges}
          renderInput={(params) => (
            <TextField 
              {...params}
              ref={(el) => { country = el; }}
              placeholder='Country'
              name='Country'
              label='Country'
              value={user.Country}
              variant='outlined'
              margin='normal'
              InputLabelProps={{
                shrink: true
              }}
              required
              error={!!errors["Country"]}
              fullWidth
              InputProps={{ ...params.InputProps, type: 'search' }}
            />
          )}
        />
        </Grid>
        <Grid item xs={6}>
          <TextField
            placeholder="Type your PAN number here"
            name='PAN'
            label='PAN Number'
            value={user.PAN}
            variant='outlined'
            InputLabelProps={{
              shrink: true
            }}
            inputProps={{
              pattern: "[A-Z]{5}[0-9]{4}[A-Z]{1}",
            }}
            // onChangeCapture={handleCardNumber}
            error={!!errors["PAN"]}
            fullWidth
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            placeholder="Type your GST Number here"
            name='GST'
            label='GST Number'
            value={user.GST}
            variant='outlined'
            InputLabelProps={{
              shrink: true
            }}
            inputProps={{
              pattern: "^[0-9]{2}[A-Z]{5}[0-9]{4}[A-Z]{1}[1-9A-Z]{1}Z[0-9A-Z]{1}$"
            }}
            error={!!errors["GST"]}
            fullWidth
          />
        </Grid>
      <Grid item xs={3}>
        <TextField ref={(el) => { countryC = el; }}
          placeholder='countrycode'
          name='countryCode'
          label='Country Code'
          value={user.countryCode}
          disabled={true}
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          error={!!errors["countryCode"]}
          required
          fullWidth
        />
      </Grid>

      <Grid item xs={9}>
        <TextField 
          placeholder='Enter Your Mobile Number here'
          type='number'
          name='mobileNumber'
          label='Mobile Number'
          value={user.mobileNumber}
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          error={!!errors["mobileNumber"]}
          inputProps={{
            min: 100000,
            max: 9999999999,
          }}
          required
          fullWidth
        />
      </Grid>
      <Grid item xs={5}>
        <TextField ref={(el) => { countryCp = el; }}
          placeholder='Enter Country Code'
          disabled={true}
          name='phoneCountryCode'
          label='Country Code'
          value={user.phoneCountryCode}
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          error={!!errors["phoneCountryCode"]}
          required
          fullWidth
        />
      </Grid>

      <Grid item xs={7}>
        <TextField
          placeholder='Enter Your Phone Number here'
          type='number'
          name='phoneNumber'
          label='Phone Number'
          value={user.phoneNumber}
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          error={!!errors["phoneNumber"]}
          inputProps={{
            min: 100000,
            max: 9999999999999,
          }}
          fullWidth
        />
      </Grid>

      <Grid item xs={6}>
        <TextField
          placeholder='Type your password here'
          name='password'
          label='Password'
          value={user.password}
          type='password'
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          required
          error={!!errors["password"]}
          inputProps={{
            minLength: 6,
            maxLength: 20
          }}
          fullWidth
        />
      </Grid>
      <Grid item xs={12} lg={6}>
        <TextField
          placeholder='Re-type your password here'
          label='Password'
          name='confirmPassword'
          value={user.confirmPassword}
          type='password'
          variant='outlined'
          margin='normal'
          InputLabelProps={{
            shrink: true
          }}
          inputProps={{
            minLength: 6,
            maxLength: 20
          }}
          error={!!errors["confirmPassword"]}
          required
          fullWidth
        />
      </Grid>
      {/* <Grid item xs={12}>
        <Autocomplete
          id="free-solo-2-demo"
          disableClearable
          options={CountryList.map((option) => option.name)}
          filterOptions={filterOptions}
          onChange={handleChanges}
          renderInput={(params) => (
            <TextField
              {...params}
              placeholder='Government Identification Type'
              name='governmentIdentificationType'
              label='Government Identification Type'
              value={user.governmentIdentificationType}
              variant='outlined'
              margin='normal'
              InputLabelProps={{
                shrink: true
              }}
              inputProps={{
                minLength: 6,
                maxLength: 20
              }}
              required
              error={!!errors["Country"]}
              fullWidth
              InputProps={{ ...params.InputProps, type: 'search' }}
            />
          )}
        />
      </Grid> */}
    </Grid>
  );
};
