import React, { useContext, Fragment } from "react";
import { UserContext } from "./UserContext";
import { makeStyles } from "@material-ui/core/styles";

//GENERAL
import {
  Typography,
  Grid,
  Checkbox,
  FormControlLabel
} from "@material-ui/core";

const useStyles = makeStyles(theme => ({
  summary: {
    padding: theme.spacing(3),
    border: "1px solid #ddd",
    marginBottom: theme.spacing(2)
  }
}));
// SUMMARY COMPONENT
export default props => {
  const classes = useStyles();
  const [state] = useContext(UserContext);
  const { fullName, email, phoneCoutryCode, mobileNumber, HAaddressLine1, HAArea, HADistrict, HAState, HACountry, HACity, HAZIP, OAaddressLine1, OAArea, OADistrict, OAState, OACountry, OACity, OAZIP,GST,PAN,designation} = state.user;
  return (
    <Fragment>
      <Grid container className={classes.summary}>
        <Grid item xs={12}>
          <Typography variant='h5'>Summary</Typography>
        </Grid>

        <Grid item xs={6}>
          <Typography variant='h6'>Name</Typography>
          <Typography variant='body2'>{fullName}</Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography variant='h6'>Designation</Typography>
          <Typography variant='body2'>{designation}</Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography variant='h6'>Email</Typography>
          <Typography variant='body2'>{email}</Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography variant='h6'>Mobile Number</Typography>
          <Typography variant='body2'>{phoneCoutryCode}{mobileNumber}</Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography variant='h6'>GST</Typography>
          <Typography variant='body2'>{GST}</Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography variant='h6'>PAN</Typography>
          <Typography variant='body2'>{PAN}</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant='h6'>Home Address</Typography>
          <Typography variant='body2'>{HAaddressLine1},{HAArea},{HADistrict},{HACity},{HAState},{HACountry},{HAZIP}</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant='h6'>Office Address</Typography>
          <Typography variant='body2'>{OAaddressLine1},{OAArea},{OADistrict},{OACity},{OAState},{OACountry},{OAZIP}</Typography>
        </Grid>
      </Grid>
      <Grid container>
        <Grid item xs={12}>
          <FormControlLabel
            control={
              <Checkbox
                checked={state.user.acceptTerms}
                required
                color='primary'
                name='acceptTerms'
              />
            }
            label='I accept terms and conditions'
          />
        </Grid>
      </Grid>
    </Fragment>
  );
};
